}

.team-text {
  text-align: center;
  letter-spacing: .02em;
  font-size: 30px;
  line-height: 1.11;
}

.team-member {
  text-align: center;
  border-radius: 0 0 4px 4px;
  height: 450px;
  box-shadow: 0 2px 1px #2e2f4214, 0 1px 1px #2e2f4229, 0 1px 6px #2e2f4214;
}

.member-names {
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding-top: 20px;
  display: flex;
}

.header-text {
  letter-spacing: .02em;
  color: #434455;
  font-size: 14px;
  line-height: 1.5;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (width >= 320px) {
